.hero-image {
     background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)), url("https://demo.wenthemes.com/travel-gem/wp-content/uploads/sites/65/2018/10/banner-9.jpg");
     height: 100%;
